___CSS_LOADER_EXPORT___.push([module.id, `.task-item {
    display: grid;
    grid-template-columns: 1fr 1fr auto;
    gap: 10px;
    background-color: var(--main-background-color);
    box-shadow: var(--main-box-shadow);
    border-radius: var(--main-border-radius);
    padding: var(--main-padding);
}

.task-item:hover {
    background-color: var(--accent-color);
    scale: 1.003;
}

.task-title {
    margin: 0;
    padding: 0;
    font-size: 20px;
}

.task-time-left {
    align-self: center;
    justify-self: end;
}

.button-container {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    display: flex;
    margin-left: 24px;
    gap: 8px;
    border: none;
    background-color: transparent;
}

.task-details {
    display: none;
    grid-column: 1 / 3;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
}

.task-description {

}

.task-date {
    justify-self: end;
}

.task-priority {

}

#edit-title-input,
#edit-desc-input,
#edit-date-input {
    box-sizing: border-box;
    width: 66%;
    min-width: 170px;
    height: 26px;
    align-self: center;
}
.edit-button-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 36px;
}

#edit-date-input {
    justify-self: end;
}

.project-confirm-button,
.project-cancel-button {
    flex-grow: 1;
    max-width: 95px;
}`, "",{"version":3,"sources":["webpack://./src/styles/task-item.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,mCAAmC;IACnC,SAAS;IACT,8CAA8C;IAC9C,kCAAkC;IAClC,wCAAwC;IACxC,4BAA4B;AAChC;;AAEA;IACI,qCAAqC;IACrC,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,iBAAiB;IACjB,QAAQ;IACR,YAAY;IACZ,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,qCAAqC;IACrC,SAAS;AACb;;AAEA;;AAEA;;AAEA;IACI,iBAAiB;AACrB;;AAEA;;AAEA;;AAEA;;;IAGI,sBAAsB;IACtB,UAAU;IACV,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,iBAAiB;AACrB;;AAEA;;IAEI,YAAY;IACZ,eAAe;AACnB","sourcesContent":[".task-item {\n    display: grid;\n    grid-template-columns: 1fr 1fr auto;\n    gap: 10px;\n    background-color: var(--main-background-color);\n    box-shadow: var(--main-box-shadow);\n    border-radius: var(--main-border-radius);\n    padding: var(--main-padding);\n}\n\n.task-item:hover {\n    background-color: var(--accent-color);\n    scale: 1.003;\n}\n\n.task-title {\n    margin: 0;\n    padding: 0;\n    font-size: 20px;\n}\n\n.task-time-left {\n    align-self: center;\n    justify-self: end;\n}\n\n.button-container {\n    grid-column: 3 / 4;\n    grid-row: 1 / 2;\n    display: flex;\n    margin-left: 24px;\n    gap: 8px;\n    border: none;\n    background-color: transparent;\n}\n\n.task-details {\n    display: none;\n    grid-column: 1 / 3;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 10px;\n}\n\n.task-description {\n\n}\n\n.task-date {\n    justify-self: end;\n}\n\n.task-priority {\n\n}\n\n#edit-title-input,\n#edit-desc-input,\n#edit-date-input {\n    box-sizing: border-box;\n    width: 66%;\n    min-width: 170px;\n    height: 26px;\n    align-self: center;\n}\n.edit-button-container {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 36px;\n}\n\n#edit-date-input {\n    justify-self: end;\n}\n\n.project-confirm-button,\n.pr